.main-goals, .about-achievement-icon-cont{
    background: #245235;    
}
.goals-header, .goals-description, .goals-text{
    color: #FFFFFF;  
}
.goals-header{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 35px;
    line-height: 71px;
    letter-spacing: 0.07em;  
}
.goals-description{
    font-family: 'Roboto';
    font-weight: 300;
    font-size: 22px;
    line-height: 38px;  
}
.goals-text, .goals-text-more{
font-family: 'Raleway';
font-weight: 700;
font-size: 16px;
line-height: 30px;
}
.goals-text-more{
    color: #FF7B19;   
    cursor: pointer; 
}
.main-founders-title, .main-founders-text{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 37px;
    line-height: 60px;
    color: #070707;    
}
.main-founders-text{
    font-size: 22px;
    line-height: 49px;    
}
.main-founders-hr{
    width: 62.03px;
    color: #070707;    
}
.main-founders-name{
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 23px;
    line-height: 60px;  
}
.campaign-search-cont{
    margin-left: auto;
}
.single-campaign-bg{
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    box-shadow: 5px 8px 3px gray;
    border: none;
    margin-bottom: 8%;
}
.campaign-caption{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 19px;
    line-height: 60px;
    color: #FFFFFF;
}
.campaign-title{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 45px;
    line-height: 53px;
    color: #FFFFFF;
}
.campaign-text{
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 24px;
    line-height: 42px;
    color: #F7F7F7;
}
.campaign-join-header, .campaign-no-filter{
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 50px;
    line-height: 57px;
    color: #4D4D4D;    
}
.campaign-join-learn-more-btn, .campaign-join-lets-do-it-btn{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 19px;
    line-height: 35px;
    border-radius: 5px; 
} 
.campaign-join-learn-more-btn{
    box-sizing: border-box;
    border: 3px solid #03544F;
    color: #03544F;      
}
.campaign-join-lets-do-it-btn{
    box-sizing: border-box;    
    background: #FF7B19;
    color: #FFFFFF;    
}
.campaign-join-icon-labels{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 25px;
    line-height: 60px;  
    color: #000000;    
}
.about-tab-title{
    font-family: 'Raleway';
    font-weight: 500;
    font-size: 24px;
    line-height: 37px;
    color: #245235;    
}
.about-overview-img-1-cont{
    border-right: 3px solid #FF7B19;
}
.about-overview-img-2-cont{
    border-left: 3px solid #FF7B19;
}
.about-overview-header-title{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 34px;
    line-height: 50px;
    letter-spacing: 0.04em;    
    color: #000000;    
}
.about-overview-header-text, .campaigns-header-text{
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 22px;
    line-height: 43px;
    letter-spacing: 0.04em;  
    color: #0B0B0B;    
}
.about-overview-header-caption, .about-overview-captions-text{
    font-family: 'Raleway';
    font-weight: 600;
    font-size: 22px;
    line-height: 37px;
    letter-spacing: 0.04em;  
    color: #000000;    
}
.about-overview-captions-title{
    background: #FF7B19;
    box-shadow: 20px 10px 5px rgba(0, 0, 0, 0.25);
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 22px;
    line-height: 55px;
    letter-spacing: 0.04em;    
    color: #F7F7F7;        
}
.about-overview-sub-header-title{
    font-family: 'Raleway';
    font-weight: 500;
    /* font-size: 99px; */
    line-height: 116px;
    letter-spacing: 0.04em;
    color: #000000; 
}
.about-overview-details-title{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 44px;
    line-height: 53px;
    letter-spacing: 0.04em;
    color: #FFFFFF;    
}
.about-overview-details-listItem{
    list-style-type: disc;
    font-family: 'Raleway';
    font-weight: 300;
    font-size: 18px;
    line-height: 31px;
    letter-spacing: 0.04em;
    color: #F7F7F7
}
.about-achievements-cont{
    margin: auto;
}
.about-achievement-title{
    font-family: 'Raleway';
    font-weight: 600;
    font-size: 43px;
    line-height: 53px;
    color: #000000;    
}
.about-achievement-text{
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 28px;
    line-height: 53px;
    color: #000000;    
}
.about-achievement-img-text{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 40px;
    line-height: 35px;
    color: #F7F7F7;    
}
.about-achievement-img-text sup{
    font-size: 40px;
    color: #FF7B19;
    transform: rotate(180deg);
}
.select-with-icon {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;    
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0;
    background-color: #D9D9D9;
    background-size: .65em auto, 100%;
    padding-right: 25px;
    padding-left: 15px;
    padding-top: 5px;
    padding-bottom: 5px;
    border: 1px solid #D9D9D9;
    border-radius: 3px;
}
.contact-main{
    background: #245235;
}  
.contact-main-form{
    background-color: #D9D9D9;
    transform: scaleY(1.3);
}
.contact-main-form-labels{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 14px;
    line-height: 53px;
    border: none;
    color: #272727;    
}
.contact-main-form-btn{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 16px;
    line-height: 35px;
    letter-spacing: 0.1em;
    color: #FFFFFF; 
    border: none;
    border-radius: 4px;   
}
.contact-main-form-links{
    font-family: 'Raleway';
    font-weight: 500;
    font-size: 15px;
    letter-spacing: 0.08em;
    color: #000000;    
}
.contact-main-header, .contact-main-sub-header{
    font-family: 'Raleway';
    font-weight: 500;
    line-height: 53px;
}
.contact-main-header{
    font-size: 54px;
    color: #F7F7F7;    
}
.contact-main-sub-header{
    font-size: 18px;
    color: #FF7B19;    
}
.contact-main-text{
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 14px;
    line-height: 25px;
    color: #F7F7F7;    
}
.yellow-line{
    border: 1px solid #FF7B19;
}

.about-achievement-header, .about-theTeam-header, .about-gallery-header, .campaigns-header{
    font-family: 'Railway';
    font-style: normal;
    font-weight: 400;
    line-height: 62px;
    color: #B0B0B0; 
    border-left: 5px solid #FF7B19; 
}

.achievements-read-more{
    font-size: 20px;
}

.theTeam-section-title{
    font-family: 'Railway';
    font-weight: 400px;
    line-height: 60px;
    font-size: 24px;
    border-bottom: 5px solid #FF7B19;
}

.theTeam-img-title{
    font-weight: bold;
    font-family: 'Railway';
    line-height: 24px;
    font-size: 24px;
}

.theTeam-img-text{
    font-family: 'Railway';
    font-weight: lighter;
    line-height: 20px;
    font-size: 17px;
}