.main-nav, .main-header, .main-founder, .main-about, .about, .contact, .campaign{
  background: #F7F7F7;
}
.main-nav-link{
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 38px;
  color: #666666;
  cursor: pointer;  
}
.main-nav-link:hover{
  color: #245235;
}
.active-main-nav-link{
  color: #FE7B19;
}
.main-header-btn, .main-nav-btn, .main-gitd-btn, .campaign-btn{
  font-family: 'Raleway';
  font-weight: 700;
  border: none;
}
.main-header-text{
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 21px;
  line-height: 42px;
  color: #666666;  
}
.main-nav-btn{
  background: #245235; 
  font-size: 16px;
  line-height: 31px;
  color: #FEFEFE; 
}
.main-header-btn, .main-contact-btn, .campaign-others-btn, .campaign-1-btn{
background: #FF7B19;  
font-size: 17px;
line-height: 40px;
color: #FFFFFF;
border: none;
}
.campaign-1-btn, .about-overview-detail{
  background: #245235;
}
.main-contact-btn, .main-gitd-btn{
  font-size: 28px;
}
.main-contact{
  background: #245235;
}
.main-contact-title{
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 600;
  font-size: 40px;
  line-height: 45px;
  letter-spacing: 0.03em;
  color: #FFFFFF;  
}
.main-contact-text{
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 21px;
  line-height: 42px;
  color: #B0B0B0;  
}
.main-footer{
  background: #0C0C0C;
}
.main-footer-navs{
  font-family: 'Raleway';
  font-weight: 700;
  font-size: 17px;
  line-height: 71px;
  color: #D9D9D9;
}
.main-footer-sub-navs{
  font-family: 'Roboto';
  font-weight: 400;
  font-size: 16px;
  line-height: 42px;
  color: #B0B0B0;  
  cursor: pointer;
}
.main-footer-sub-navs:hover{
  color: #FF7B19;
}
.main-footer-header{
  font-family: 'Bai Jamjuree';
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #F7F7F7;  
}
.main-footer-powered-by{
  font-family: 'Bai Jamjuree';
  font-weight: 400;
  font-size: 22px;
  line-height: 37px;
  letter-spacing: 0.09em;  
  color: #35353C;
}
.main-footer-cg{
  font-family: 'Bai Jamjuree';
  font-weight: 700;
  font-size: 22px;
  line-height: 39px;
  letter-spacing: 0.2em;
  color: #D9D9D9;  
}
.main-footer-copyright{
  font-family: 'Inter';
  font-weight: 400;
  font-size: 12px;
  line-height: 23px;
  letter-spacing: 0.09em;
  color: #D9D9D9;  
}
.main-about-caption, .main-about-header{
  font-family: 'Raleway';
}
.main-about-caption{
  font-weight: 600;
  font-size: 25px;
  line-height: 71px;
  color: #4D4D4D;  
}
.main-about-header{
  font-weight: 700;
  font-size: 32px;
  line-height: 59px;
  letter-spacing: 0.04em;
  color: #000000;  
}
.main-about-text{
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 25px;
  line-height: 55px;
  letter-spacing: 0.04em;
  color: #0B0B0B;  
}
.main-about-nav-text{
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 25px;  
}
.main-about-nav-text sup{
  font-size: 30px;
  font-weight: bolder;
}
.main-about-nav-sub-text{
  font-family: 'Raleway';
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}
.main-about-oversized-img{
  max-width: 90vw;
  min-height: 40vh;
}
.main-nav-caption-hr{
  border: 3px solid #FE7B19;  
}
.main-nav-text-division{
  font-family: 'Raleway';
  font-weight: 400;
  font-size: 17px;
  line-height: 25px;
  letter-spacing: 0.1em;
  color: #4D4D4D;  
}
.main-about-caption-link{
  background: #245235;
  border-radius: 1px;  
}
.main-games-in-the-dumb, .contact-main-form-btn{
  background: #FF7B19;
}
.main-gitd-caption,.main-gitd-header, .main-gitd-btn, .main-gitd-sub-header,
.main-gitd-text, .main-gitd-sub-text {
  color: #FFFFFF; 
  font-family: 'Raleway';
  font-weight: 700;
}
.main-gitd-btn{
  background: #245235;
  border-radius: 5px;
  font-size: 19px;
  line-height: 42px;
  letter-spacing: 0.02em;   
}
.main-gitd-caption{
  font-size: 19px;
}
.main-gitd-header{
  font-size: 35px;
}
.main-gitd-sub-header{
  font-size: 22px;
  line-height: 60px; 
}
.main-gitd-text{
  font-size: 21px;
  line-height: 42px;
  color: #FEFEFE;  
}
.main-gitd-sub-text{
  font-size: 15px;
  line-height: 42px;
  letter-spacing: 0.02em; 
}
.main-active-blog-post-preview-link{
  background-color: red;
}